import { html } from 'lit';
import type { Meta, StoryObj } from '@storybook/web-components';

const meta: Meta = {
  title: 'Charts/Heatmap',
};

export default meta;

const generateData = (seriesCount: number, dataPoints: number) =>
  Array.from({ length: seriesCount }, (_, s) => ({
    name: `Metric ${s + 1}`,
    data: Array.from({ length: dataPoints }, (_, i) => ({
      x: `W${i + 1}`,
      y: Math.floor(Math.random() * 100),
    })),
  }));

export const Basic: StoryObj = {
  render: () => {
    setTimeout(() => {
      import('apexcharts').then((ApexCharts) => {
        const chart = new ApexCharts.default(
          document.querySelector('#heatmap'),
          {
            chart: { type: 'heatmap', height: 350 },
            colors: ['#008FFB'],
            series: generateData(7, 10),
            dataLabels: { enabled: false },
            title: { text: 'Heatmap in Storybook' },
          }
        );
        chart.render();
      });
    }, 0);
    return html`<div id="heatmap"></div>`;
  },
};
